body{
  background-color: aliceblue;
}

button{
  display: inline-flex !important;
  align-items: center;
  justify-content: center;
}
.header{
  width: 100%;
  height: 200px;
  margin-bottom: 20px;
  position: relative;
}
.header img{
  object-fit: cover;
  object-position: center;
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 10;
}
.header .text-box{
  position: absolute;
  z-index: 11;
  bottom: 20%;
}
.header h1 a{
  font-size: 2vw;
  color: #FAFAFA;
  text-decoration: none;
}

.header p{
  font-size: 1.2vw;
  color: rgb(224, 225, 240);
  margin: 0;
}
.post-item{
    margin-bottom: 20px;
}

.post-item .footer{
    display: flex;
    align-items: center;
    justify-content: space-between
}

.post-item .toolbar{
    display: flex;
    align-items: center;
    justify-content: space-between
}

.post-item .toolbar .publish-info,
.post-item .toolbar .comment-count{
    display: flex;
    align-items: center;
}

.post-item.vote-score-container{
  display: flex;
  align-items: center;
}
.post-item .vote-score-container .down{
    color: red;
}
.post-item .vote-score-container .up{
    color: green;
}
.post-item .vote-score-container span{
  cursor: pointer;
}
.toolbar{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sort-icon{
  cursor: pointer;
}
.edit-user{
    cursor: pointer;
}
.categories header{
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.post .actions{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.post .vote-score-container{
  display: flex;
  align-items: center;
}
.post .vote-score-container span{
  cursor: pointer;
}
.post .vote-score-container .down{
    color: red;
}
.post .vote-score-container .up{
    color: green;
}
.post .actions button{
  min-width: 80px;
}
.post .actions button:last-child{
  margin-left: 10px;
}

.post-item .toolbar{
  display: flex;
  align-items: center;
  justify-content: space-between
}

.post .toolbar .publish-info,
.post .toolbar .comment-count{
  display: flex;
  align-items: center;
}
.comment-box h3{
  display: flex;
  align-items: center;
}
.comment-box button{
  display: inline-flex;
  align-items: center;
  justify-content: center
}
.comment-box button:hover,
.comment-box button:active,
.comment-box button:focus{
  text-decoration: none;
}
.comment{
  background-color: white;
  padding: 10px;
  margin-bottom: 30px;
  border-radius: 4px;
  box-shadow: 1px 1px 6px 0px #888;
}
.comment-actions{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

